









export default {
	data() {
		return {
			title: '',
			strings: '',
			conten_url: ''
		};
	},
	onLoad: function(e) {
		//console.log(e);
		uni.showLoading({
			title: '加载中...',
			//mask: false
		});
		uni.request({
			url: 'https://news-at.zhihu.com/api/4/news/' + e.id,
			method: 'GET',
			data: {},
			success: res => {
				this.strings = res.data.body;
				console.log(this.strings);
				this.strings = this.strings.replace("“", '"');
				this.strings = this.strings.replace("”", '"');
				this.conten_url = res.data.share_url;
				//console.log(res);
				
				
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	}
}



